.wrapper {
    min-height: 100vh;
    position: relative;
    max-width: 100vw;
    margin: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
}

.main {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    margin-bottom: 5vh;
}

.alert {
    /*display: none;*/
    opacity: 0;
    visibility: hidden;
}
.show {
    visibility: visible;
    opacity: 1;
}
